:root {
  --main-color: #088b81;
  --blue: #0000ff;
  --dark-blue: #18293c;
  --orange: #ffa500;
  --green-yellow: #cddc39;
  --pink-light: #efa2b4;
  --cyan-light: #aef1ee;
  --white: #ffffff;
  --white-alpha-40: rgba(255, 255, 255, 0.4);
  --white-alpha-25: rgba(255, 255, 255, 0.25);
  --backdrop-filter-blur: blur(5px);
}

body {
  background-image: linear-gradient(
    to bottom right,
    var(--pink-light),
    var(--cyan-light)
  );
  font-size: 16px;
  color: var(--dark-blue);
  line-height: 1.5;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;

  font-weight: 300;
  background-attachment: fixed;
}

/*background circles */

.bg-circles {
  position: fixed;
  top: 0;
  height: 100%;
  max-width: 1200px;
  width: calc(100% - 30px);
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
}

@keyframes zoomInOut {
  0%,
  100% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1);
  }
}

@keyframes bounceTop {
  0%,
  100% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(0px);
  }
}

@keyframes bounceLeft {
  0%,
  100% {
    transform: translateX(50px);
  }
  50% {
    transform: translateX(0px);
  }
}

.bg-circles div {
  position: absolute;
  border-radius: 50%;
}

.bg-circles .circle-1 {
  height: 60px;
  width: 60px;
  background-color: var(--cyan-light);
  top: 10%;
  left: 5%;
  animation: zoomInOut 5s linear infinite;
  opacity: 0.5;
}
.bg-circles .circle-5 {
  height: 60px;
  width: 60px;
  background-color: var(--blue);
  top: 80%;
  left: 20%;
  animation: zoomInOut 5s linear infinite;
  opacity: 0.5;
}
.bg-circles .circle-6 {
  height: 60px;
  width: 60px;
  background-color: var(--orange);
  top: 20%;
  left: 50%;
  animation: bounceLeft 5s ease-in-out infinite;
  opacity: 0.5;
}
.bg-circles .circle-7 {
  height: 80px;
  width: 80px;
  background-color: var(--dark-blue);
  top: 85%;
  left: 75%;
  animation: bounceTop 5s ease-in-out infinite;
  opacity: 0.5;
}
.bg-circles .circle-8 {
  height: 60px;
  width: 60px;
  background-color: var(--pink-light);
  top: 28%;
  left: 90%;
  animation: zoomInOut 5s linear infinite;
  opacity: 1;
}

.bg-circles .circle-2 {
  height: 80px;
  width: 80px;
  background-color: var(--main-color);
  left: 40%;
  top: 50%;
  animation: bounceTop 5s ease-in-out infinite;
  opacity: 0.5;
}

.bg-circles .circle-3 {
  height: 90px;
  width: 90px;
  background-color: var(--white);
  right: -45px;
  top: 40%;
  opacity: 0.5;
}

.bg-circles .circle-4 {
  height: 50px;
  width: 50px;
  background-color: var(--green-yellow);
  left: -30px;
  top: 80%;
  opacity: 0.5;
  opacity: 0.6;
}

.input-control {
  display: block;
  max-width: 400px;
  height: 50px;
  border-radius: 5px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  background-color: var(--white-alpha-25);
  padding: 0 20px;
  transition: all 0.3s ease;
  outline: none;
  border: 2px solid hsla(0, 0%, 100%, 0.2);
  color: var(--dark-blue);
}

button {
  -webkit-user-select: none;
  user-select: none;
}

.btn {
  line-height: 1.5;
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  padding: 10px 28px;
  display: inline-block;
  border-radius: 30px;
  color: var(--main-color);
  font-weight: 500;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
  overflow: hidden;
  vertical-align: middle;
  transition: color 0.3s ease;
}

.btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0%;
  background-color: var(--main-color);
  z-index: -1;
  transition: width 0.3s ease;
}

.btn:hover::before {
  width: 100%;
}

.btn:hover {
  color: var(--white);
}

.login_form {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_form_inside {
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  max-width: 500px;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 30px;
  backdrop-filter: var(--backdrop-filter-blur);
  margin: auto;
}

.user_components {
  background-color: var(--white-alpha-25);
  border: 1px solid var(--white-alpha-40);
  border-radius: 80px;
  backdrop-filter: var(--backdrop-filter-blur);
  margin: auto;
  padding: 8px 20px;
  align-items: center;
}
